.dogCards {
    display: grid;
    align-items: top;
    grid-template-columns: 1fr 1fr 1fr;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 30px;
    row-gap: 30px;
    height: 100%;
    max-width: 1080px;
    margin: 0 auto;
    
}

@media (max-width: 768px) {
    .dogCards {
        grid-template-columns: 1fr 1fr;
    } 
}

@media (max-width: 454px) {
    .dogCards {
        grid-template-columns: 1fr;
    } 
}

